<template>
  <div class="training-info" id="training-info">
    <div class="" v-for="empl in info" :key="empl.id" id="train-empl">
      <div class="infos__h3">
        <h3>{{ empl.name }}</h3>
      </div>
      <div class="training-info__row">
        <div class="training-info__column">
          <div class="training-info__item infos">
            <!-- <img :src="require('../assets/img/' + empl.img)" alt="img" /> -->
            <img
              :src="require('../assets/img/empl/' + empl.img)"
              alt="Сотрудник"
              width="250"
              height="250"
            />
            <div class="infos__h2">
              <router-link :to="`/company/${empl.article}`"
                ><h2>{{ empl.empl }}</h2></router-link
              >
            </div>
          </div>
        </div>
        <div class="training-info__column">
          <div class="training-info__item">
            <div class="infos__course" v-if="empl.price">
              <h3>
                Цена:<span> {{ empl.price }}</span>
              </h3>
            </div>
            <div class="infos__course" v-if="empl.time">
              <h3>
                Время:<span> {{ empl.time }}</span>
              </h3>
            </div>
            <div class="infos__course" v-if="empl.place">
              <h3>
                Место:<span> {{ empl.place }}</span>
              </h3>
            </div>
          </div>
        </div>
      </div>
      <div class="infos__course2" v-if="empl.description">
        <h3>
          Описание курса:<span> {{ empl.description }}</span>
        </h3>
      </div>
      <div class="infos__btn">
        <div class="product__btn btn3">
          <a href="#popup2" class="popup-link">
            <input
              class="btn3__span"
              type="button"
              value="Записаться"
              @mousedown="nameproductfun(empl), popup()"
          /></a>
        </div>
      </div>
      <div id="popup2" class="popup2">
        <div class="popup2__body">
          <div class="popup2__content">
            <div class="popup2__container">
              <div class="popup2__title">
                <a href="#" class="popup2__close close-popup">x</a>
              </div>
              <div class="popup2__text">
                <p class="popup2__text-p">Запись</p>
                <form action="" class="contact__form block" method="POST">
                  <div class="block_info">
                    <p>Ваши Имя и Фамилия</p>
                    <input
                      type="text"
                      id="name"
                      v-model="name"
                      @blur="$v.name.$touch()"
                      placeholder="Иваи Иванов"
                    />
                    <span v-if="$v.name.$error" class="span-error">
                      <template v-if="!$v.name.maxLength">
                        Длина имени не должна превышать
                        {{ $v.name.$params.maxLength.max }} символов
                      </template>
                      <template v-else-if="!$v.name.alpha">
                        Введите Имя и Фамилию
                      </template>
                    </span>
                  </div>
                  <div class="block_info">
                    <p>Ваш номер телефона</p>
                    <input
                      type="text"
                      id="phone"
                      v-model="phone"
                      v-mask="'+7(###)###-####'"
                      placeholder="+7(###)###-####"
                      @blur="$v.phone.$touch()"
                    />
                    <span v-if="$v.phone.$error" class="span-error">
                      <template v-if="!$v.phone.minLength">
                        Номер введен некорректно!
                      </template>
                    </span>
                  </div>
                  <div class="block_info">
                    <p>Электронная почта</p>
                    <input
                      type="text"
                      id="mail"
                      v-model="mail"
                      @blur="$v.mail.$touch()"
                      placeholder="example@email.com"
                    />
                    <span v-if="$v.mail.$error" class="span-error">
                      <template v-if="!$v.mail.validFormat">
                        Укажите корректный адрес электронной почты.
                      </template>
                    </span>
                  </div>
                  <span v-if="$v.$invalid" class="span-button">
                    <template>
                      <p>Перед отправкой данный заполните все поля!</p>
                    </template>
                  </span>
                  <div class="block__btn btn2" type="submit">
                    <a href="#popup" @mousedown="index">
                      <button
                        type="submit"
                        value="Отправить"
                        class="btn2__span"
                        :disabled="$v.$invalid"
                        @mousedown="postPost(name, mail, phone, newempl)"
                      >
                        Отправить
                      </button></a
                    >
                    <!-- <a href="#popup" @mousedown="index"><input type="button" value="Отправить" class="btn2__span" @click="postPost(name,mail,phone,productname)"></a> -->
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import popups from "@/popup/popup.js";
import axios from "axios";
import {
  required,
  maxLength,
  minLength,
  email,
} from "vuelidate/lib/validators";
export default {
  name: "training-info",
  data() {
    return {
      name: null,
      mail: null,
      phone: null,
      newempl: [],
    };
  },
  props: {
    info: {
      type: Array,
      default() {
        return {};
      },
    },
  },
  validations: {
    mail: {
      validFormat: (val) =>
        /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/.test(
          val
        ),
    },
    name: {
      required,
      maxLength: maxLength(30),
      alpha: (val) => /^[а-яё]+(?: |, |-)+[а-яё]*$/i.test(val),
    },
    phone: {
      minLength: minLength(15),
    },
  },
  methods: {
    postPost(name, mail, phone, newempl) {
      try {
        let data = axios.post("http://31.31.203.119/api/orders", {
          name: name,
          mail: mail,
          phone: phone,
          productname: newempl[0].name,
        });
        if (data.status == "success") {
          console.log("Data deliveried");
        } else {
          throw new Error(data.status);
        }
      } catch (e) {
        this.error = e;
      }
    },
    nameproductfun(empl) {
      this.newempl.push({
        name: empl.name,
      });
      // console.log(this.product);
    },

    index() {
      this.$router.push("/");
    },
    popup() {
      const popupLinks = document.querySelectorAll(".popup-link");
      const body = document.querySelector("body");
      const lockPadding = document.querySelectorAll(".lock-padding");
      console.log(popupLinks);
      let unlock = true;

      const timeout = 800;

      if (popupLinks.length > 0) {
        for (let index = 0; index < popupLinks.length; index++) {
          const popupLink = popupLinks[index];
          popupLink.addEventListener("click", function (e) {
            const popupName = popupLink.getAttribute("href").replace("#", "");
            const curentPopup = document.getElementById(popupName);
            popupOpen(curentPopup);
            e.preventDefault();
          });
        }
      }
      const popupCloseIcon = document.querySelectorAll(".close-popup");
      if (popupCloseIcon.length > 0) {
        for (let index = 0; index < popupCloseIcon.length; index++) {
          const el = popupCloseIcon[index];
          el.addEventListener("click", function (e) {
            popupClose(el.closest(".popup2"));
            e.preventDefault();
          });
        }
      }

      function popupOpen(curentPopup) {
        if (curentPopup && unlock) {
          const popupActive = document.querySelector(".popup2.open");
          if (popupActive) {
            popupClose(popupActive, false);
          } else {
            bodyLock();
          }
          curentPopup.classList.add("open");
          curentPopup.addEventListener("click", function (e) {
            if (!e.target.closest(".popup2__content")) {
              popupClose(e.target.closest(".popup2"));
            }
          });
        }
      }

      function popupClose(popupActive, doUnlock = true) {
        if (unlock) {
          popupActive.classList.remove("open");
          if (doUnlock) {
            bodyUnLock();
          }
        }
      }

      function bodyLock() {
        const lockPaddingValue =
          window.innerWidth -
          document.querySelector(".wrapper").offsetWidth +
          "px";

        if (lockPadding.length > 0) {
          for (let index = 0; index < lockPadding.length; index++) {
            const el = lockPadding[index];
            el.style.paddingRight = lockPaddingValue;
          }
        }
        body.style.paddingRight = lockPaddingValue;
        body.classList.add("lock");

        unlock = false;
        setTimeout(function () {
          unlock = true;
        }, timeout);
      }

      function bodyUnLock() {
        setTimeout(function () {
          if (lockPadding.length > 0) {
            for (let index = 0; index < lockPadding.length; index++) {
              const el = lockPadding[index];
              el.style.paddingRight = "0px";
            }
          }
          body.style.paddingRight = "0px";
          body.classList.remove("lock");
        }, timeout);

        unlock = false;
        setTimeout(function () {
          unlock = true;
        }, timeout);
      }

      document.addEventListener("keydown", function (e) {
        if (e.which === 27) {
          const popupActive = document.querySelector(".popup.open");
          popupClose(popupActive);
        }
      });

      (function () {
        // проверяем поддержку
        if (!Element.prototype.closest) {
          // реализуем
          Element.prototype.closest = function (css) {
            var node = this;
            while (node) {
              if (node.matches(css)) return node;
              else node = node.parentElement;
            }
            return null;
          };
        }
      })();
      (function () {
        // проверяем поддержку
        if (!Element.prototype.matches) {
          // определяем свойство
          Element.prototype.matches =
            Element.prototype.matchesSelector ||
            Element.prototype.webkitMatchesSelector ||
            Element.prototype.mozMatchesSelector ||
            Element.prototype.msMatchesSelector;
        }
      })();
    },
  },
  mounted() {
    // export default popup;
  },
};
</script>

