<template>
  <tbody>
    <tr @click="finditem(), scrollToEnd()">
      <td aria-label="Тема" class="info-td">
        {{ itemservices_data.name }}
      </td>
      <td aria-label="Время" class="contact-td">
        {{ itemservices_data.time }}
      </td>
      <td aria-label="Формат" class="contact-td">
        {{ itemservices_data.format }}
      </td>
      <td aria-label="Преподаватель" class="contact-td">
        {{ itemservices_data.empl }}
      </td>
    </tr>
  </tbody>
</template>

<script>
export default {
  name: "table",
  props: {
    itemservices_data: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {};
  },
  methods: {
    finditem() {
      this.$emit("finditem", this.itemservices_data);
    },
    scrollToEnd: function () {
      let slide = document.getElementById("training-info");

      let top = window.scrollY + slide.getBoundingClientRect().y;

      window.scrollTo({
        top,
        behavior: "smooth",
      });
    },
  },
  mounted: function () {
    // this.$emit("finditem", this.itemservices_data);
  },
};
</script>

