</template>

<script>
import TrainingItem from "@/components/training-item.vue";
import TrainingInfo from "@/components/training-info.vue";
import TrainingTable from "@/components/training-table.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "index",
  data() {
    return {
      info: [],
      pageNumber: 0,
    };
  },
  props: {
    size: {
      type: Number,
      required: false,
      default: 4,
    },
  },
  components: {
    TrainingTable,
    TrainingItem,
    TrainingInfo,
  },
  computed: {
    ...mapGetters(["SERVICES"]),

    pageCount() {
      let l = this.SERVICES.length,
        s = this.size;
      return Math.ceil(l / s);
    },
    paginatedData() {
      const start = this.pageNumber * this.size,
        end = start + this.size;
      return this.SERVICES.slice(start, end);
    },
  },
  methods: {
    ...mapActions(["GET_SERVICES"]),
    finditem(itemservices_data) {
      this.info = [];
      this.info.splice(itemservices_data, 1);
      this.info.push({
        id: itemservices_data.id,
        name: itemservices_data.name,
        format: itemservices_data.format,
        empl: itemservices_data.empl,
        aboutempl: itemservices_data.aboutempl,
        img: itemservices_data.img,
        price: itemservices_data.price,
        time: itemservices_data.time,
        place: itemservices_data.place,
        description: itemservices_data.description,
      });
    },
    nextPage() {
      this.pageNumber++;
    },
    prevPage() {
      this.pageNumber--;
    },
  },
  mounted() {
    this.info.push({
      _id: "5f809267976a7d186093513a",
      description: "",
      empl: "Тужани Ислам",
      format: "Семинар",
      id: "2",
      img: "1.jpg",
      place: null,
      price: "",
      time: "09.10.2020",
      name: "Основы дерматологии для врачей косметологов (дерматологов) ",
    }),
      this.$emit("finditem", this.info),
      this.GET_SERVICES()
        .then((response) => {
          if (response.data) {
          }
        })
        .catch((error) => {
          console.log(error);
        });
  },
};
</script>